import * as React from "react";
import Box from "@mui/material/Box";
import Button from "@mui/material/Button";
import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import DialogContentText from "@mui/material/DialogContentText";
import DialogTitle from "@mui/material/DialogTitle";
import FormControl from "@mui/material/FormControl";
import MenuItem from "@mui/material/MenuItem";
import Select from "@mui/material/Select";
import { FaThumbsDown } from "react-icons/fa";

import Snackbar from "@mui/material/Snackbar";
import MuiAlert, { AlertProps } from "@mui/material/Alert";

const Alert = React.forwardRef<HTMLDivElement, AlertProps>(function Alert(
  props,
  ref
) {
  return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
});

function InCorrectButton() {
  const [openDiaglog, setOpenDiaglog] = React.useState(false);
  const [open, setOpen] = React.useState(false);

  //
  //
  const handleClickOpen = () => {
    setOpenDiaglog(true);
  };
  //
  const handleClose = () => {
    setOpenDiaglog(false);
  };

  //snack
  const handleClick = () => {
    setOpen(true);
  };
  const handleCloseSnack = (
    event?: React.SyntheticEvent | Event,
    reason?: string
  ) => {
    if (reason === "clickaway") {
      return;
    }
    setOpen(false);
    setOpenDiaglog(false);
  };

  return (
    <React.Fragment>
      <Button
        variant="outlined"
        startIcon={<FaThumbsDown />}
        size="medium"
        color="error"
        onClick={handleClickOpen}
      >
        
      </Button>
      <Dialog open={openDiaglog} onClose={handleClose}>
        <DialogTitle></DialogTitle>
        <DialogContent>
          <DialogContentText>
            
            
          </DialogContentText>
          <Box
            noValidate
            component="form"
            sx={{
              display: "flex",
              flexDirection: "column",
              m: "auto",
              width: "fit-content"
            }}
          >
            <FormControl sx={{ mt: 5, minWidth: 150 }}>
              <p></p>
              <Select autoFocus defaultValue>
                <MenuItem value="neu">
                   <span></span>
                </MenuItem>
                <MenuItem value="pos">
                   <span></span>
                </MenuItem>
                <MenuItem value="neg">
                   <span></span>
                </MenuItem>
                <MenuItem value="q">
                   <span></span>
                </MenuItem>
              </Select>
            </FormControl>
          </Box>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClick}></Button>
          <Snackbar
            open={open}
            autoHideDuration={500}
            onClose={handleCloseSnack}
            anchorOrigin={{
              vertical: "bottom",
              horizontal: "center"
            }}
          >
            <Alert
              onClose={handleCloseSnack}
              severity="success"
              sx={{ width: "100%" }}
            >
              
            </Alert>
          </Snackbar>
          <Button onClick={handleClose}></Button>
        </DialogActions>
      </Dialog>
    </React.Fragment>
  );
}

export default InCorrectButton;
